@mixin foundation-form-checkbox {
  [type='file'],
  [type='checkbox'],
  [type='radio'] {
    margin: 0 0 $form-spacing;
  }

  [type='checkbox'] + label,
  [type='radio'] + label {
    display: inline-block;
    margin-#{$global-left}: $form-spacing * 0.5;
    margin-#{$global-right}: $form-spacing;
    margin-bottom: 0;
    vertical-align: baseline;

    &[for] {
      cursor: pointer;
    }
  }

  label > [type='checkbox'],
  label > [type='radio'] {
    margin-#{$global-right}: $form-spacing * 0.5;
  }

  [type='file'] {
    width: 100%;
  }
}
