.main-navigation {
  margin-top: 25px;
}

.menu {
  &-upper {
    font-size: .857rem;
    text-align: right;

    li {
      margin: 3px 6px;
    }
  }

  &-main {
    font-size: 13px;
    padding: 0 10px;
    border: 2px solid $white;
    border-radius: 10px;
    background: $primary-color;

    & > ul {
      display: flex;

      & > li {
        position: relative;
        padding: 5px 0;
        flex-grow: 1;
        text-align: center;

        &.active,
        &:hover {
          background: $secondary-color;

          &:after,
          &:before {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            height: 2px;
            background: $secondary-color;
          }

          &:before {
            top: -2px;
          }

          &:after {
            bottom: -2px;
          }
        }

        &:hover .submenu {
          display: block;
        }
      }
    }

    .sub-submenu,
    .submenu {
      display: none;
      text-align: left;

      li {
        display: block;
        padding: 0 10px;
        line-height: 1.7rem;

        &:hover {
          background: $secondary-color;

          a {
            color: $white;

            &:before{
              content: url('data:image/png;base64,');
            }
          }
        }
      }

      a {
        color: $secondary-color;

        &:before {
          content: url('data:image/png;base64,');
          position: relative;
          left: 5px;
          padding-right: 5px;
        }
      }
    }

    .submenu {
      width: 160px;
      position: absolute;
      top: calc(100% + 15px);
      border: 1px solid $secondary-color;
      background: $white;
      padding: 5px 0;

      &:before {
        content: '';
        display: block;
        height: 17px;
        background: transparent;
        position: absolute;
        left: 0;
        right: 0;
        top: -17px;
      }

      & > li > a:before {
        left: 0;
      }
    }

    .active > ul,
    .has-submenu:hover > ul {
      display: block;
    }
  }

  &-secondary {
    display: flex;
    padding: 5px;
    color: $secondary-color;

    & > ul {
      flex-grow: 1;
      margin-left: 5px;
    }
  }
}
