body{
  font-family: Arial;
  font-size: 14px;
}

.page-wrapper{
  max-width: 1020px;
  margin: 0 auto;
  background:
    url('../images/clouds-top.png') no-repeat 50% 0%,
    url('../images/clouds-bottom.png') no-repeat 50% 100%,
    linear-gradient(
      to bottom,
      $primary-color,
      $white 39%,
      $white 93%,
      $primary-color);
}

.main-content{
  & > *{
    &:not(:first-child):not(:last-child){ padding: 0 4px; }
    &:first-child{ padding-right: 4px; }
    &:last-child{ padding-left: 4px; }
  }

}